@media (max-width: 945px) {
    .latest-work__items {
        gap: 1.7vw;
    }
}
@media (max-width: 655px) {
    header .container {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .main-title .container {
        padding-top: 90px;
        padding-bottom: 120px;
    }
    .header__title {
        font-size: 1.2rem;
    }
    .main-title h1 {
        max-width: 360px;
        font-size: 2.2rem;
    }
    .latest-work h2 {
        font-size: 1.7rem;
    }
    .latest-work .container {
        padding-bottom: 85px;
    }
    .latest-work__items {
        grid-template-columns: repeat(2, 1fr);
        gap: 2.4vw;
    }
    .latest-work__items img:last-of-type {
        grid-column: 1 / span 2;
    }
}
@media (max-width: 540px) {
    .header__nav {
        position: fixed;
        background-color: #313131; 
        display: block;
        width: 300px;
        height: 100vh;
        top: 0;
        left: -300px;
        transition: transform .5s;
        padding: 0;
        z-index: 2;
        border-radius: 0;
    }
    .header__nav li a {
        display: block;
        padding: 15px 100px 15px 30px;
        color: var(--white);
        font-size: 1.4rem;
        display: block;
    }
    .header__burger-btn {
        display: block;
    }
    header.open .header__nav {
        transform: translateX(100%);
    }
    .main-title .container {
        padding-top: 60px;
        padding-bottom: 90px;
    }
    .header__nav li a:hover::before {
        content: none;
    }
}
@media (max-width: 515px) {
    .main-title h1 {
        font-size: 2rem;
        max-width: 252px;
    }
}
@media (max-width: 470px) {
    .latest-work__items {
        gap: 1.5vw;
    }
}
@media (max-width: 400px) {
    .latest-work .container {
        padding-bottom: 35px;
    }
    .latest-work__items {
        grid-template-columns: 1fr;
        gap: 3vw;
    }
    .latest-work__items img:last-of-type {
        grid-column: 1;
    }
}
@media (max-width: 380px) {
    .header__nav {
        width: 250px;
        left: -250px;
    }
}
@media (max-width: 330px) {
    .header__nav {
        width: 200px;
        left: -200px;
    }
    .main-title h1 {
        font-size: 1.5rem;
    }
}