.defaultBtn {
    background: transparent;
    font-size: 0.813rem;
    color: var(--light-green);
    text-transform: uppercase;
    padding: 10px 26px;
    border: 1px solid var(--light-green);
    border-radius: 3px;
    cursor: pointer;
    transition: .3s;
}
.defaultBtn:hover {
    background: var(--white);
    font-weight: 700;
    box-shadow: inset 0 0 0 1px var(--light-green);
}
.defaultBtn:active {
    background: var(--white);
    font-weight: 700;
    box-shadow: inset 0 0 0 1px var(--black);
    color: var(--black);
    transition: none;
}