.App {
    background: var(--almost-black);
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    overflow: hidden;
}

.container {
    --container-w: 905px;
    max-width: calc(var(--container-w) + (var(--container-p) * 2));
    width: 100%;
    margin: 0 auto;
    --container-p: 20px;
    padding: 0 var(--container-p);
}

.main-bg {
    background: url('../../public/img/main-bg.png') bottom / cover no-repeat;
    position: relative;
}

header {
    background: transparent;
}
header .container {
    padding: 43px var(--container-p);
    display: flex;
    justify-content: space-between;
    gap: 40px;
    align-items: center;
}
.header__burger-btn {
    display: none;
    position: absolute;
    right: var(--container-p);
    min-width: 40px;
    min-height: 40px;
    background: rgba(0, 0, 0, .6);
    border-radius: 4px;
    cursor: pointer;
    z-index: 2;
}
.header__burger-btn .line {
    position: absolute;
    width: 30px;
    height: 3px;
    background: var(--light-green);
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
    transition: .5s;
    border-radius: 100px;
}
.header__burger-btn .first-line {
    transform: translateY(calc(-50% - 10px));
}
.header__burger-btn .last-line {
    transform: translateY(calc(-50% + 10px));
}
header.open .header__burger-btn .mid-line {
    opacity: 0;
}
header.open .header__burger-btn .first-line {
    transform: translateY(0) rotate(-45deg);
}
header.open .header__burger-btn .last-line {
    transform: translateY(0) rotate(45deg);
}
header.open .header__burger-btn {
    background: var(--white);
}
header.open .header__burger-btn .line {
    background: var(--black);
}
header.open .header__nav-wrapper {
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
    background: rgba(0, 0, 0, .3);
}

.header__title {
    font-size: 1.5rem;
    color: var(--white);
    position: relative;
    padding: 10px 10px;
    background: rgba(0, 0, 0, .6);
    border-radius: 4px;
}
.header__title span {
    position: relative;
    z-index: 1;
}
.header__title:active {
    color: var(--light-green);
}
.header__title .decor {
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 100%;
}
.header__title .decor::before {
    content: '';
    position: absolute;
}
.header__title .bottom-line::before {
    border-radius: 100px;
    background: var(--light-green);
    bottom: 0; left: 0;
    width: 0%; height: 2px;
    transition: .3s;
    z-index: 1;
}
.header__title:hover .bottom-line::before {
    width: 100%;
}
.header__title:active .bottom-line::before {
    width: 100%;
    transition: none;
}
.header__title .left-line::before {
    border-radius: 100px;
    background: var(--light-green);
    bottom: 0; left: 0;
    width: 2px; height: 0%;
    transition: .3s;
    z-index: 1;
}
.header__title:hover .left-line::before {
    height: 100%;
}
.header__title:active .left-line::before {
    height: 100%;
    transition: none;
}
.header__title .active-bg::before {
    border-top-right-radius: 4px;
    bottom: 0; left: 0;
    width: 0%; height: 0%;
    background: var(--white);
    z-index: 0;
}
.header__title:active .active-bg::before {
    width: 100%; height: 100%;
    background: var(--white);
}
.header__nav {
    padding: 10px 0;
    background: rgba(0, 0, 0, .6);
    border-radius: 4px;
    display: flex;
}
.header__nav li a {
    text-transform: uppercase;
    color: var(--grey);
    position: relative;
    transition: .2s;
    padding: 10px 20px;
}
.header__nav li a.color {
    color: var(--light-green);
}
.header__nav li a:hover {
    text-shadow: 2px 2px 4px var(--light-green);
}
.header__nav li a:hover::before {
    content: '';
    position: absolute;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 0;
}
.header__nav li a:hover::before {
    border-left: 12px solid transparent;
	border-right: 12px solid transparent;
	border-bottom: 20px solid var(--light-green);
}
.header__nav li a:active {
    background: var(--light-green);
    color: var(--black);
    font-weight: 700;
    text-shadow: none;
    transition: none;
}
.main-title {
    background: transparent;
}
.main-title .container {
    padding: 110px var(--container-p) 140px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.main-title h1 {
    max-width: 470px;
    font-size: 3rem;
    color: var(--white);
    margin-bottom: 29px;
}

main {
    flex: 1 1 auto;
}

.latest-work {
    background: transparent;
}
.latest-work .container {
    padding: 49px var(--container-p) 101px;
    text-align: center;
}
.latest-work h2 {
    margin-bottom: 33px;
    font-size: 1.563rem;
    color: var(--white);
}
.latest-work__items {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 19px;
    justify-content: center;
}
.latest-work__items img {
    width: 100%;
    background: #cecece;
    height: auto;
    border-radius: 3px;
    object-fit: cover;
}

footer {
    background: var(--almost-black);
}
footer .container {
    padding: 35px var(--container-p) 37px;
    text-align: center;
}
footer h2 {
    color: var(--light-grey);
    font-size: 1.125rem;
}
footer p {
    margin-top: 10px;
    color: var(--grey);
    font-size: 0.75rem;
}